const renderAdditionalCSS = (content) =>{
  let head = document.head || document.getElementsByTagName('head')[0];
  let style = document.createElement('style');

  style.type = 'text/css';
  
  if(style.styleSheet){
    style.styleSheet.cssText = content; 
  }else{
    style.appendChild(document.createTextNode(content));
  }

  return head.appendChild(style);
}


const renderDom = (component, state) =>{
  if (component === undefined || component === null) {
     R(H(state.component, state), state.mountPoint);
  }else{
     R(H(component, state), state.mountPoint);
  }
}

const main = () =>{
  let Component = MainPage;
  let mountPoint = document.getElementById('mount-point');
  let newState = Object.assign(state, {
    component: MainPage,
    mountPoint: mountPoint,
    tags: [],
    roles: [],
    routes: '/',
    cssContent: cssContent,
  });
  renderAdditionalCSS(cssContent);
  renderDom(Component, newState);
}


document.body.onload = main;

